const SPECIAL_CHARS_REGEXP = /([\:\-\_]+(.))/g; // 
function camelCase(name) {
  return name.replace(SPECIAL_CHARS_REGEXP, function(
    _,
    separator,
    letter,
    offset
  ) {
    // toUpperCase 
    return offset ? letter.toUpperCase() : letter;
  });
}

/**
 * 
 * @param el 
 * @param styleName 
 */
export const getStyle = (el, styleName) => {
  if (!el || !styleName) return;
  styleName = camelCase(styleName);
  if (styleName === "float") {
    styleName = "cssFloat";
  }
  try {
    const computed = document.defaultView.getComputedStyle(el, "");
    return el.style[styleName] || computed ? computed[name] : null;
  } catch (e) {
    return el.style[styleName];
  }
};

/**
 * scrollTop animation 
 * @param el 
 * @param from 
 * @param to 
 * @param duration 
 * @param endCallBack
 */
export const scrollTop = (el, from = 0, to, duration = 500, endCallBack) => {
  // requestAnimationFrame => 
  if (!window.requestAnimationFrame) {
    window.requestAnimationFrame =
      window.webkitRequestAnimationFrame ||
      window.mozRequestAnimationFrame ||
      window.msRequestAnimationFrame ||
      function(callback) {
        return window.setTimeout(callback, 1000 / 60);
      };
  }

  const diff = Math.abs(from - to); // 
  const step = Math.ceil((diff / duration) * 50); //

  /*  */
  function scroll(start, end, step) {
    // 
    if (start === end) {
      endCallBack && endCallBack();
      return;
    }
    let d = start + step > end ? end : start + step;
    if (start > end) {
      d = start - step < end ? end : start - step;
    }

    if (el === window) {
      window.scrollTo(d, d);
    } else {
      el.scrollTop = d;
    }
    //  => setInterval
    window.requestAnimationFrame(() => scroll(d, end, step));
  }
  scroll(from, to, step);
};

/*   */
export const findComponentUpward = (
  VueContext,
  componentName,
  componentNames
) => {
  if (typeof componentName === "string") {
    componentNames = [componentName];
  } else {
    componentNames = componentName;
  }

  let parent = VueContext.$parent;
  let name = parent.$options.name;
  while (parent && (!name || componentNames.indexOf(name) < 0)) {
    parent = parent.$parent;
    if (parent) name = parent.$options.name;
  }
  return parent;
};

/*   */
export const findComponentsUpward = (VueContext, componentName) => {
  let parents = [];
  const parent = VueContext.$parent;
  if (parent) {
    if (parent.$options.name === componentName) parents.push(parent);
    return parents.concat(findComponentsUpward(parent, componentName));
  } else {
    return [];
  }
};

/**
 * 
 */
export const findBrothersComponents = (
  VueContext,
  componentName,
  exceptThis = true
) => {
  let res = VueContext.$parent.$children.filter(item => {
    return item.$options.name === componentName;
  });
  // 
  if (exceptThis) {
    const index = res.findIndex(item => item._uid === VueContext._uid);
    res.splice(index, 1);
  }
  return res;
};

/*   */
export const findComponentDownward = (VueContext, componentName) => {
  const childrens = VueContext.$children;
  let children = null;

  if (childrens.length) {
    for (let child of childrens) {
      const name = child.$options.name;
      // 
      if (name === componentName) {
        children = child;
        break;
      } else {
        children = findComponentDownward(child, componentName);
        if (children) break;
      }
    }
  }

  return children;
};

/*   */
export const findComponentsDownward = (VueContext, componentName) => {
  return VueContext.$children.reduce((components, child) => {
    if (child.$options.name === componentName) components.push(child);
    const foundChilds = findComponentsDownward(child, componentName);
    return components.concat(foundChilds);
  }, []);
};

/* props */
export const CheckProps = (value, checkList = []) => {
  for (let i = 0; i < checkList.length; i++) {
    if (value === checkList[i]) {
      return true;
    }
  }
  return false;
};

export const sharpMatcherRegx = /#([^#]+)$/; // #
