import Vue from "vue";
import LoadingBar from "./main.vue";

LoadingBar.newInstall = properties => {
  const _props = properties || {};
  /*  */
  const Instance = new Vue({
    data: _props,
    render(h) {
      return h(LoadingBar, {
        props: _props
      });
    }
  });

  const component = Instance.$mount(); // 
  document.body.appendChild(component.$el); // 
  const loading_bar = Instance.$children[0]; // LoadingBar InstallVue

  return {
    component: loading_bar,
    /*  */
    update(options) {
      // options => object
      if ("percent" in options) {
        loading_bar.percent = options.percent;
      }
      if (options.status) {
        loading_bar.status = options.status;
      }
      if ("show" in options) {
        loading_bar.visible = options.show;
      }
    },
    /*  */
    destroy() {
      document.body.removeChild(
        document.getElementsByClassName("c-loading-bar")[0]
      );
    }
  };
};

export default LoadingBar;
