<template>
  <div :class="classes" :style="styles">
    <div :class="childClasses"><slot></slot></div>
    <slot name="trigger">
      <div
        v-show="showBottomTrigger"
        @click="toggleCollapse"
        :class="triggerClasses"
        :style="{ width: siderWidth + 'px' }"
      >
        <i :class="triggerIconClasses"></i>
      </div>
    </slot>
  </div>
</template>

<script>
const prefixClass = "c-layout-sider";
export default {
  name: "CSider",
  props: {
    value: {
      type: Boolean,
      default: false
    },
    width: {
      type: [String, Number],
      default: 200
    },
    // 
    hideTrigger: {
      type: Boolean,
      default: false
    },
    // 
    collapsible: {
      type: Boolean,
      default: false
    },
    // 
    collapsedWidth: {
      type: [Number, String],
      default: 64
    }
  },
  data() {
    return {
      prefixClass,
      mediaMatched: false,
      reverseArrow: false // 
    };
  },
  computed: {
    classes() {
      return [`${prefixClass}`];
    },
    styles() {
      return {
        width: `${this.siderWidth}px`,
        minWidth: `${this.siderWidth}px`,
        maxWidth: `${this.siderWidth}px`,
        flex: `${this.siderWidth}px`
      };
    },
    childClasses() {
      return `${prefixClass}-children`;
    },
    // 
    showBottomTrigger() {
      return this.collapsible ? !this.mediaMatched && !this.hideTrigger : false;
    },
    //  
    siderWidth() {
      return this.collapsible
        ? this.value
          ? this.mediaMatched
            ? 0
            : parseInt(this.collapsedWidth)
          : parseInt(this.width)
        : this.width;
    },
    triggerClasses() {
      return [
        `${this.prefixClass}-trigger`,
        this.value ? `${this.prefixClass}-trigger-collapsed` : ""
      ];
    },
    triggerIconClasses() {
      return [
        `code-icon-arrow`,
        `${this.prefixClass}-icon-${this.reverseArrow ? "forward" : "back"}`,
        `${this.prefixClass}-trigger-icon`
      ];
    }
  },
  methods: {
    toggleCollapse() {
      let value = this.collapsible ? !this.value : false;
      this.$emit("input", value); // inputv-model
    }
  },
  watch: {
    value(val) {
      this.$emit("on-collapse", val);
    }
  }
};
</script>
