<template>
  <div :class="wrapClasses">
    <template v-if="type !== 'textarea'">
      <!-- clear -->
      <c-icon
        name="error"
        :class="[
          `${prefixCls}-icon`,
          `${prefixCls}-icon-${this.size}`,
          `${prefixCls}-icon-clear `
        ]"
        v-if="clearable && currentValue"
        @click="handleClear"
      />
      <!-- icon -->
      <c-icon
        v-else-if="icon"
        :class="[`${prefixCls}-icon`]"
        :name="icon"
        @click="handleIconClick"
      />
      <!-- search -->
      <c-icon
        name="search"
        :class="[`${prefixCls}-icon`, `${prefixCls}-ios-search`]"
        v-else-if="search && searchButton === false"
        @click="handleSearch"
      />
      <!-- Icon -->
      <span :class="[`${prefixCls}-with-prefix`]" v-else-if="showPrefix">
        <slot name="prefix"> <c-icon :name="prefix" /> </slot>
      </span>
      <!--  -->
      <div :class="[`${prefixCls}-group-prepend`]" v-if="prepend">
        <slot name="prepend"></slot>
      </div>
      <input
        :id="elementId"
        :type="type"
        :name="name"
        :class="inputClasses"
        :value="currentValue"
        :placeholder="placeholder"
        :disabled="disabled"
        :maxlength="maxlength"
        :readonly="readonly"
        :autocomplete="autocomplete"
        :spellcheck="spellcheck"
        :autofocus="autofocus"
        ref="input"
        @input="handleInput"
        @focus="handleFocus"
        @blur="handleBlur"
        @change="handleChange"
        @keydown="handleKeydown"
        @keypress="handleKeypress"
        @keyup.enter="handleEnter"
        @keyup.exact="handleKeyup"
        @compositionstart="handleComposition"
        @compositionupdate="handleComposition"
        @compositionend="handleComposition"
      />
      <!-- Icon -->
      <span :class="[`${prefixCls}-with-suffix`]" v-if="showSuffix">
        <slot name="suffix"> <c-icon :name="suffix" /> </slot>
      </span>
      <!--  -->
      <div :class="[`${prefixCls}-group-append`]" v-if="append">
        <slot name="append"></slot>
      </div>
      <!-- search -->
      <div
        :class="[`${prefixCls}-group-append`, `${prefixCls}-search`]"
        v-if="search && searchButton"
        @click="handleSearch"
      >
        <c-icon name="search" v-if="searchButton === true" />
        <template v-else>{{ searchButton }}</template>
      </div>
    </template>
    <textarea
      v-else
      :id="elementId"
      :class="textareaClasses"
      :style="textareaStyles"
      :name="name"
      :wrap="wrap"
      :autocomplete="autocomplete"
      :spellcheck="spellcheck"
      :placeholder="placeholder"
      :value="value"
      :disabled="disabled"
      :rows="rows"
      :readonly="readonly"
      :maxlength="maxlength"
      ref="textarea"
      @input="handleInput"
      @focus="handleFocus"
      @blur="handleBlur"
      @change="handleChange"
      @keydown="handleKeydown"
      @keypress="handleKeypress"
      @keyup.enter="handleEnter"
      @keyup.exact="handleKeyup"
      @compositionstart="handleComposition"
      @compositionupdate="handleComposition"
      @compositionend="handleComposition"
    >
    </textarea>
  </div>
</template>

<script>
import CIcon from "@/components/icon/src/main.vue";
import { CheckProps } from "@/utils/assets.js";
import calcTextareaHeight from "@/utils/calcTextareaHeight.js";
const prefixCls = "c-input";
export default {
  name: "CInput",
  props: {
    // 
    size: {
      validator(value) {
        return CheckProps(value, ["small", "large", "default"]);
      },
      default: "default"
    },
    // 
    type: {
      validator(value) {
        return CheckProps(value, [
          "text",
          "password",
          "url",
          "email",
          "textarea"
        ]);
      },
      default: "text"
    },
    name: {
      type: String
    },
    //  c-icon
    icon: {
      type: String
    },
    // ID
    elementId: {
      type: String
    },
    value: {
      type: [Number, String],
      default: ""
    },
    placeholder: {
      type: String,
      default: "Enter something..."
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    },
    // Icon
    prefix: {
      type: String,
      default: ""
    },
    // Icon
    suffix: {
      type: String,
      default: ""
    },
    // 
    maxlength: {
      type: Number
    },
    // 
    clearable: {
      type: Boolean,
      default: false
    },
    // 
    search: {
      type: Boolean,
      default: false
    },
    // 
    searchButton: {
      type: [Boolean, String],
      default: false
    },
    //  => 
    spellcheck: {
      type: Boolean,
      default: false
    },
    // 
    autocomplete: {
      validator(value) {
        return CheckProps(value, ["on", "off"]);
      },
      default: "on"
    },
    // 
    readonly: {
      type: Boolean,
      default: false
    },
    // 
    autofocus: {
      type: Boolean,
      default: false
    },
    // Textarea
    // 
    autosize: {
      type: [Boolean, Object],
      default: false
    },
    // 
    wrap: {
      validator(value) {
        return CheckProps(value, ["hard", "soft"]);
      },
      default: "soft"
    },
    // 
    rows: {
      type: Number,
      default: 2
    }
  },
  data() {
    return {
      prefixCls: prefixCls,
      currentValue: this.value,
      prepend: true,
      append: true,
      showSuffix: false,
      showPrefix: false,
      isOnComposition: false, // 
      textareaStyles: {}
    };
  },
  computed: {
    wrapClasses() {
      return [
        `${prefixCls}-wrapper`,
        {
          [`${prefixCls}-group`]:
            this.prepend || this.append || (this.search && this.searchButton),
          [`${prefixCls}-group-${this.size}`]: !!this.size,
          [`${prefixCls}-group-with-append`]:
            this.append || (this.search && this.searchButton),
          [`${prefixCls}-group-with-prepend`]: this.prepend,
          [`${prefixCls}-group-with-${this.size}`]:
            !!this.size &&
            (this.append || this.prepend || (this.search && this.searchButton))
        }
      ];
    },
    inputClasses() {
      return [
        `${prefixCls}`,
        {
          [`${prefixCls}-${this.size}`]: !!this.size, // 
          [`${prefixCls}-disabled`]: this.disabled,
          [`${prefixCls}-suffix`]: this.showSuffix, // 
          [`${prefixCls}-prefix`]: this.showPrefix // 
        }
      ];
    },
    textareaClasses() {
      return [
        `${prefixCls}`,
        {
          [`${prefixCls}-disabled`]: this.disabled
        }
      ];
    }
  },
  methods: {
    // icon
    handleIconClick(event) {
      this.$emit("on-click", event);
    },
    // Input
    handleInput(event) {
      if (this.isOnComposition) return;

      let value = event.target.value;
      this.$emit("input", value); // v-model
      this.setCurrentValue(value);
      this.$emit("on-change", event);
    },
    // Input
    handleChange(event) {
      this.$emit("on-input-change", event);
    },
    // 
    handleClear(event) {
      this.$emit("input", "");
      this.setCurrentValue("");
      this.$emit("on-change", event);
    },
    // 
    handleFocus(event) {
      this.$emit("on-focus", event);
    },
    // 
    handleBlur(event) {
      this.$emit("on-blur", event);
    },
    // 
    handleEnter(event) {
      this.$emit("on-enter", event);
      if (this.search) this.$emit("on-search", this.currentValue);
    },
    // 
    handleKeydown(event) {
      this.$emit("on-keydown", event);
    },
    //  
    handleKeypress(event) {
      this.$emit("on-keypress", event);
    },
    // 
    handleKeyup(event) {
      this.$emit("on-keyup", event);
    },
    // 
    handleSearch() {
      if (this.disabled) return;
      this.$refs.input.focus();
      this.$emit("on-search", this.currentValue);
    },
    // 
    handleComposition(event) {
      if (event.type === "compositionstart") {
        this.isOnComposition = true;
      }
      if (event.type === "compositionend") {
        this.isOnComposition = false;
        this.handleInput(event);
      }
    },
    // 
    setCurrentValue(value) {
      if (value === this.currentValue) return;
      this.$nextTick(() => {
        this.resizeTextarea();
      });
      this.currentValue = value;
    },
    // 
    resizeTextarea() {
      const autosize = this.autosize;
      if (!autosize || this.type !== "textarea") {
        return false;
      }
      const minRows = autosize.minRow;
      const maxRows = autosize.maxRow;

      this.textareaStyles = calcTextareaHeight(
        this.$refs.textarea,
        minRows,
        maxRows
      );
    },
    focus() {
      this.$refs.input.focus();
    },
    blur() {
      this.$refs.input.blur();
    }
  },
  watch: {
    value(val) {
      this.setCurrentValue(val);
    }
  },
  mounted() {
    this.showSuffix = this.suffix !== "" || this.$slots.suffix !== undefined;
    this.showPrefix = this.prefix !== "" || this.$slots.prefix !== undefined;
    this.prepend = this.$slots.prepend !== undefined;
    this.append = this.$slots.append !== undefined;
  },
  components: {
    CIcon
  }
};
</script>
