import CLoadingBar from "./src/main.js";

let loadingBarInstance;
let color = "primary";
let failedColor = "error";
let height = 2;
let timer;

/* loadingBar */
function getLoadingBarInstance() {
  loadingBarInstance =
    loadingBarInstance ||
    CLoadingBar.newInstall({
      color,
      failedColor,
      height
    });
  return loadingBarInstance;
}

/*  */
function update(options) {
  let instace = getLoadingBarInstance();

  instace.update(options);
}

/*  */
function clearTimer() {
  if (timer) {
    clearInterval(timer);
    timer = null;
  }
}

/*  */
function hide() {
  setTimeout(() => {
    update({
      show: false
    });
    setTimeout(() => {
      update({
        percent: 0
      });
    }, 200);
  }, 800);
}

export default {
  // TODO loadingBar
  start() {
    if (timer) return;

    let percent = 0; // 
    /*  */
    update({
      percent,
      status: "primary",
      show: true
    });

    timer = setInterval(() => {
      percent += Math.floor(Math.random() * 4); // 5%
      // 
      if (percent > 90) {
        clearTimer();
      }
      // 
      update({
        percent: percent,
        status: "primary",
        show: true
      });
    }, 300);
  },
  // TODO loadingBar  - 
  finish() {
    clearTimer(); // 
    update({
      percent: 100,
      status: "finish",
      show: true
    });
    hide(); // 
  },
  // TODO loadingBar  - 
  success() {
    clearTimer(); // 
    update({
      percent: 100,
      status: "success",
      show: true
    });
    hide(); // 
  },
  // TODO loadingBar  - 
  error() {
    clearTimer(); // 
    update({
      percent: 100,
      status: "error",
      show: true
    });
    hide(); // 
  },
  // TODO loadingBar
  config(options) {
    if (options.color) {
      color = options.color;
    }
    if (options.failedColor) {
      failedColor = options.failedColor;
    }
    if (options.height) {
      height = options.height;
    }
  },
  // TODO LoadingBar
  update(percent) {
    clearTimer(); // 
    update({
      percent: percent,
      status: "finish",
      show: true
    });
  },
  // TODO LoadingBar
  destroy() {
    clearTimer();
    let instace = getLoadingBarInstance();
    loadingBarInstance = null;
    instace.destroy(); // 
  }
};
