<template>
  <ul :class="classes" :style="styles">
    <slot></slot>
  </ul>
</template>

<script>
import broadcast from "@/mixins/broadcast.js"; // mixin
import {
  CheckProps,
  findComponentsDownward,
  findComponentsUpward
} from "@/utils/assets.js";
const prefixClass = "c-menu";
export default {
  name: "CMenu",
  mixins: [broadcast],
  props: {
    //  
    mode: {
      validate(value) {
        return CheckProps(value, ["horizontal", "vertical"]);
      },
      default: "vertical"
    },
    // 
    theme: {
      validate(value) {
        return CheckProps(value, ["light", "dark", "primary"]);
      },
      default: "light"
    },
    // MenuItem
    activeName: {
      type: [String, Number]
    },
    // subMenu
    openNames: {
      type: Array,
      default: () => {
        return [];
      }
    },
    // 
    accordion: {
      type: Boolean,
      default: false
    },
    // 
    collapsedMenu: {
      type: Boolean,
      default: false
    },
    width: {
      type: String,
      default: "240px"
    }
  },
  data() {
    return {
      currentActiveName: this.activeName,
      openedNames: [] // MenuSub
    };
  },
  computed: {
    classes() {
      let theme = this.theme;
      if (this.theme === "primary" && this.mode === "vertical") theme = "light";
      return [
        `${prefixClass}`,
        `${prefixClass}-${theme}`,
        {
          [`${prefixClass}-${this.mode}`]: this.mode,
          "collapsed-menu": this.collapsedMenu
        }
      ];
    },
    styles() {
      return this.mode === "vertical" ? `width: ${this.width}` : "";
    }
  },
  methods: {
    // MenuSub
    updateOpenKeys(name) {
      let names = [...this.openedNames];
      let index = names.indexOf(name);

      // 
      if (this.accordion) {
        findComponentsDownward(this, "CMenuSub").forEach(item => {
          item.opened = false;
        });
      }
      // 
      if (index >= 0) {
        let currentMenuSub = null;
        // MenuCMenuSubname
        findComponentsDownward(this, "CMenuSub").forEach(item => {
          if (item.name === name) {
            currentMenuSub = item;
            item.opened = false;
          }
        });
        findComponentsUpward(currentMenuSub, "CMenuSub").forEach(item => {
          item.opened = true;
        });
        findComponentsDownward(currentMenuSub, "CMenuSub").forEach(item => {
          item.opened = false;
        });
      } else {
        if (this.accordion) {
          let currentMenuSub = null;
          findComponentsDownward(this, "CMenuSub").forEach(item => {
            if (item.name === name) {
              currentMenuSub = item;
              item.opened = true;
            }
          });
          findComponentsUpward(currentMenuSub, "CMenuSub").forEach(item => {
            item.opened = true;
          });
        } else {
          findComponentsDownward(this, "CMenuSub").forEach(item => {
            if (item.name === name) item.opened = true;
          });
        }
      }
      let openedNames = findComponentsDownward(this, "CMenuSub")
        .filter(item => item.opened)
        .map(item => item.name);
      this.openedNames = [...openedNames];
      this.$emit("on-open-change", openedNames);
    },
    // 
    updateActiveName() {
      if (this.currentActiveName === undefined) {
        this.currentActiveName = -1;
      }
      // 
      this.broadcast("CMenuSub", "on-update-active-name", false);
      this.broadcast(
        "CMenuItem",
        "on-update-active-name",
        this.currentActiveName
      );
    },
    /* MenuSub */
    updateOpened() {
      const items = findComponentsDownward(this, "CMenuSub");
      if (items.length) {
        items.forEach(item => {
          item.opened = this.openedNames.indexOf(item.name) > -1;
        });
      }
    },
    /* MenuItem */
    ListenMenuItemSelect() {
      this.$on("on-menu-item-select", name => {
        this.currentActiveName = name;
        this.$emit("on-select", name);
      });
    },
    handleEmitSelectEvent(name) {
      this.$emit("on-select", name);
    }
  },
  mounted() {
    this.updateActiveName();
    this.openedNames = [...this.openNames];
    this.updateOpened();
    this.ListenMenuItemSelect();
  },
  watch: {
    openNames(names) {
      this.openedNames = names;
    },
    activeName(val) {
      this.currentActiveName = val;
    },
    currentActiveName() {
      this.updateActiveName();
    }
  }
};
</script>
