<template>
  <component
    :is="wrapperComponent"
    :offset-top="offsetTop"
    :offset-bottom="offsetBottom"
  >
    <div :class="`${prefixClass}-warpper`" :style="wrapperStyle">
      <div :class="prefixClass">
        <div :class="`${prefixClass}-ink`">
          <span
            :class="`${prefixClass}-ink-ball`"
            :style="{ top: `${inkTop}px` }"
          ></span>
        </div>
        <slot></slot>
      </div>
    </div>
  </component>
</template>

<script>
import { ListenOn, ListenOff } from "@/utils/dom.js";
import {
  scrollTop,
  findComponentsDownward,
  sharpMatcherRegx
} from "@/utils/assets.js";
const prefixClass = "c-anchor";
export default {
  name: "CAnchor",
  provide() {
    return {
      CanchorCom: this
    };
  },
  props: {
    affix: {
      type: Boolean,
      default: true
    },
    offsetTop: {
      type: Number,
      default: 0
    },
    offsetBottom: Number,
    // 
    bounds: {
      type: Number,
      default: 5
    },
    // 
    scrollOffset: {
      type: Number,
      default: 0
    },
    container: null
  },
  data() {
    return {
      prefixClass,
      isAffixed: false,
      inkTop: 0, // 
      animating: false, // 
      wrapperTop: 0, // 
      scrollContainer: null, // 
      scrollElement: null, // 
      currentLink: "", //  #href
      currentId: "", //  => #id
      titlesOffsetArr: [], // 
      upperFirstTitle: true // 
    };
  },
  computed: {
    wrapperComponent() {
      return this.affix ? "CAffix" : "div";
    },
    // window
    containerIsWindow() {
      return this.scrollContainer === window;
    },
    wrapperStyle() {
      return {
        maxHeight: this.offsetTop ? `calc(100vh - ${this.offsetTop})` : "100vh"
      };
    }
  },
  methods: {
    // hrefid
    handleHashChange() {
      let url = window.location.href;
      const sharpLinkMatch = sharpMatcherRegx.exec(url);
      if (!sharpLinkMatch) return;
      this.currentLink = sharpLinkMatch[0]; // link
      this.currentId = sharpLinkMatch[1]; // ID
    },
    // 
    handleScrollTo() {
      const anchor = document.getElementById(this.currentId); // null
      const currentLinkElement = document.querySelector(
        `a[data-href="${this.currentLink}"]`
      );
      let offset = this.scrollOffset;
      if (currentLinkElement) {
        offset = parseFloat(
          currentLinkElement.getAttribute("data-scroll-offset")
        );
      }
      if (!anchor) return;
      // 
      const offsetTop = anchor.offsetTop - this.wrapperTop - offset;
      this.animating = true; // 
      // 
      scrollTop(
        this.scrollContainer,
        this.scrollElement.scrollTop,
        offsetTop,
        600,
        () => {
          this.animating = false;
        }
      );
      this.handleSetInkTop();
    },
    // ing
    handleScroll(e) {
      this.upperFirstTitle =
        e.target.scrollTop < this.titlesOffsetArr[0]
          ? this.titlesOffsetArr[0].offset
          : false;
      // 
      if (this.animating) return;
      this.updateTitleOffset();
      const scrollTop =
        document.documentElement.scrollTop ||
        document.body.scrollTop ||
        e.target.scrollTop;
      // Title ID
      this.getCurrentScrollAtTitleId(scrollTop);
    },
    // 
    handleSetInkTop() {
      const currentLinkElement = document.querySelector(
        `a[data-href="${this.currentLink}"]`
      );
      if (!currentLinkElement) return;
      const elementTop = currentLinkElement.offsetTop;
      const top = elementTop < 0 ? this.offsetTop : elementTop;
      // top
      this.inkTop = top;
    },
    // Title
    updateTitleOffset() {
      const links = findComponentsDownward(this, "CAnchorLink").map(link => {
        return link.href;
      });
      const idArr = links.map(link => {
        return link.split("#")[1];
      });
      let offsetArr = [];
      idArr.forEach(id => {
        const titleELe = document.getElementById(id);
        if (titleELe) {
          offsetArr.push({
            link: `#${id}`,
            offset: titleELe.offsetTop - this.scrollElement.offsetTop
          });
        }
      });
      this.titlesOffsetArr = offsetArr;
    },
    // Title ID
    getCurrentScrollAtTitleId(scrollTop) {
      let i = -1;
      let len = this.titlesOffsetArr.length;
      let titleItem = {
        link: "#",
        offset: 0
      };
      scrollTop += this.bounds; // 
      // 
      while (++i < len) {
        let currentEle = this.titlesOffsetArr[i];
        let nextEle = this.titlesOffsetArr[i + 1];
        if (
          scrollTop >= currentEle.offset &&
          scrollTop < ((nextEle && nextEle.offset) || Infinity)
        ) {
          titleItem = this.titlesOffsetArr[i];
          break;
        }
      }
      this.currentLink = titleItem.link;
      // 
      this.handleSetInkTop();
    },
    // &
    getContainer() {
      // 
      this.scrollContainer = this.container
        ? typeof this.container === "string"
          ? document.querySelector(this.container)
          : this.container
        : window;
      // 
      this.scrollElement = this.container
        ? this.scrollContainer
        : document.documentElement || document.body;
    },
    // 
    removeListener() {
      ListenOff(this.scrollContainer, "scroll", this.handleScroll);
      ListenOff(window, "hashchange", this.handleHashChange); // 
    },
    // 
    init() {
      this.handleHashChange();
      // Dom
      this.$nextTick(() => {
        this.removeListener();
        this.getContainer();
        this.wrapperTop = this.containerIsWindow
          ? 0
          : this.scrollElement.offsetTop; // 
        this.handleScrollTo(); // 
        this.handleSetInkTop(); // 
        this.updateTitleOffset(); // 
        this.upperFirstTitle =
          this.scrollElement.scrollTop < this.titlesOffsetArr[0]
            ? this.titlesOffsetArr[0].offset
            : false;
        // 
        ListenOn(this.scrollContainer, "scroll", this.handleScroll); // 
        ListenOn(window, "hashchange", this.handleHashChange); // 
      });
    }
  },
  watch: {
    $route() {
      this.handleHashChange();
      this.$nextTick(() => {
        this.handleScrollTo();
      });
    },
    container() {
      this.init();
    }
  },
  mounted() {
    this.init();
  },
  beforeDestroy() {
    // 
    this.removeListener();
  }
};
</script>
